.card{
    display: flex;
    flex-direction: column;
    background-color:#95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform .3s; 
}

.card:hover{
    transform: scale(1.05);
}